import uniGrid from '@/components/uni-grid/uni-grid.vue';
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue';
export default {
	components: {
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			mainBtn: [
				//三个便捷按钮
				{
					text: '我的钥匙',
					mark: '一键开门超方便',
					colors: 'colorOrange',
					url: ''
				},
				{
					text: '报事报修',
					mark: '足不出户享服务',
					colors: 'colorRed',
					url: 'main/maintenance/maintenance'
				},
				{
					text: '我要缴费',
					mark: '线上缴费超便捷',
					colors: 'colorPurple',
					url: 'main/est-pay/est-pay'
				}
			],
			banners:['/static/img/17231512438674646.jpg','/static/img/20671509093063387.jpg','/static/img/22361508750251427.jpg','/static/img/23231508750601881.jpg'],
			service: [
				{ img: '/static/img/main/78761498124328542.png', title: '电子礼品卡', mark: '家乐福9折' },
				{ img: '/static/img/main/78761498124328542.png', title: '手机维修', mark: '手机维修' },
				{ img: '/static/img/main/72581498124372327.png', title: '滴滴出行', mark: '滴滴出行3.0' }
			],
			list: [
				{
					url: 'main/notice/notice',
					text: '通知公告',
					src: '/static/img/main/notice.jpg'
				},
				{
					url: 'main/maintenance/maintenance',
					text: '报事报修',
					src: '/static/img/main/maintenance.jpg'
				},
				{
					url: '',
					text: '投诉建议',
					src: '/static/img/main/feedback.jpg'
				},
				{
					url: 'main/call/call',
					text: '联系物业',
					src: '/static/img/main/call.jpg'
				},
				{
					url: 'main/est-pay/est-pay',
					text: '物业缴费',
					src: '/static/img/main/property-pay.jpg'
				},
				{
					url: '',
					text: '房屋租售',
					src: '/static/img/main/rental.jpg'
				},
				{
					url: '',
					text: '球场预约',
					src: '/static/img/main/subscribe-stadium.jpg'
				},
				{
					url: '',
					text: '小区活动',
					src: '/static/img/main/activity.jpg'
				}
			]
		};
	},
	methods: {
		getNav(url) {
			if (!this.$store.state.hasBoundHouse) {
				uni.showModal({
					content: '请先绑定房产',
					success(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/reg/bound-house/bound-house'
							});
						}
					}
				});
				return;
			}
			if(!url){
				uni.showToast({
					icon:'none',
					title: '正在开发...'
				});
				return ;
			}
			uni.navigateTo({
				url: '/pages/' + url
			});
		}
	},
	onLoad() {
		
	},
	onPullDownRefresh() {
	        console.log('refresh');
	        setTimeout(function () {
	            uni.stopPullDownRefresh();
	        }, 1000);
	    }
};
